import { Component, Input, OnChanges, OnDestroy, SimpleChanges } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { RX_RECORD_DEFINITION, RxFieldDefinitionService } from '@helix/platform/record/api';
import { IFormControlComponent, IFormFocusable } from '@helix/platform/shared/api';
import { ValueAccessor } from '@helix/platform/shared/components';
import { RxModalService } from '@helix/platform/ui-kit';
import { OpenViewActionModalSize } from '@helix/platform/view/api';
import { get, has, noop } from 'lodash';
import { BehaviorSubject, forkJoin } from 'rxjs';
import { defaultIfEmpty, map, shareReplay, switchMap, take } from 'rxjs/operators';
import { RxRecordGridUtilsService } from '../../../common/services/record-grid-utils.service';
import { RX_RECORD_GRID } from '../../../record-grid.constant';
import { RxRecordGridConfigUtilsService } from '../../../runtime/services/record-grid-config-utils.service';
import { RecordGridDesignValidationIssue } from '../../record-grid-design.types';
import { RecordGridColumnEditorModalComponent } from './record-grid-column-editor-modal/record-grid-column-editor-modal.component';
import {
  IRecordGridColumnEditorControlOptions,
  IRecordGridColumnEditorModalParams,
  IRecordGridColumnEditorModalResult,
  IRecordGridDesignColumnData,
  IRecordGridDesignInspectorColumnConfig
} from './record-grid-column-editor.types';

@Component({
  selector: 'rx-record-grid-column-editor',
  templateUrl: './record-grid-column-editor-control.component.html',
  styleUrls: ['./record-grid-column-editor-control.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: RecordGridColumnEditorControlComponent,
      multi: true
    }
  ]
})
export class RecordGridColumnEditorControlComponent
  extends ValueAccessor<IRecordGridDesignInspectorColumnConfig[]>
  implements IFormControlComponent, IFormFocusable, OnChanges, OnDestroy
{
  @Input()
  options: IRecordGridColumnEditorControlOptions;

  private updateColumnsSubject = new BehaviorSubject<IRecordGridDesignInspectorColumnConfig[]>([]);

  columns$ = this.updateColumnsSubject.pipe(
    switchMap((inspectorColumns) =>
      forkJoin(
        inspectorColumns.map((inspectorColumn) =>
          forkJoin([
            this.rxRecordGridUtilsService.getAssociationDescriptor(
              inspectorColumn.fieldId,
              this.options.recordDefinition
            ),
            this.rxRecordGridUtilsService.getFieldDefinition(inspectorColumn.fieldId, this.options.recordDefinition)
          ]).pipe(
            map(([associationDescriptor, fieldDefinition]) => ({
              associationDescriptor,
              fieldDefinition,
              inspectorColumn
            }))
          )
        )
      ).pipe(
        defaultIfEmpty([]),
        map((columnsData) =>
          columnsData.map((columnData): IRecordGridDesignColumnData => {
            return {
              ...columnData.inspectorColumn,
              fieldDefinition: columnData.fieldDefinition,
              associationDescriptor: columnData.associationDescriptor,
              title: columnData.inspectorColumn.title?.trim(),
              searchable: has(columnData.inspectorColumn, 'searchable')
                ? this.rxRecordGridConfigUtilsService.getBooleanValue(columnData.inspectorColumn.searchable)
                : columnData.inspectorColumn.filterable &&
                  this.rxRecordGridUtilsService.isSearchable(columnData.fieldDefinition, this.options.recordDefinition),
              fallbackTitle: `[${columnData.fieldDefinition.name}]`
            };
          })
        ),
        shareReplay(1)
      )
    )
  );

  constructor(
    private rxModalService: RxModalService,
    private rxFieldDefinitionService: RxFieldDefinitionService,
    private rxRecordGridConfigUtilsService: RxRecordGridConfigUtilsService,
    private rxRecordGridUtilsService: RxRecordGridUtilsService
  ) {
    super();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (
      changes.options?.previousValue &&
      (changes.options.currentValue as IRecordGridColumnEditorControlOptions).recordDefinition.name !==
        (changes.options.previousValue as IRecordGridColumnEditorControlOptions).recordDefinition.name
    ) {
      this.updateColumnsSubject.next(this.value);
    }
  }

  onWriteValue(inspectorColumns: IRecordGridDesignInspectorColumnConfig[]): void {
    this.updateColumnsSubject.next(inspectorColumns);
  }

  removeColumn(columnData: IRecordGridDesignColumnData): void {
    this.updateValue(this.value.filter((column) => column.fieldId !== columnData.fieldId));
  }

  updateValue(columns: IRecordGridDesignInspectorColumnConfig[]): void {
    this.value = columns;
    this.onWriteValue(columns);
  }

  openEditor(columnGuid?: string, activeActionIndex?: number): void {
    this.columns$.pipe(take(1)).subscribe((columns) => {
      const data: IRecordGridColumnEditorModalParams = {
        isReadOnly: this.isDisabled,
        columns,
        activeColumn: columns.find((item) => item.guid === columnGuid),
        recordDefinition: this.options.recordDefinition,
        activeActionIndex
      };

      this.rxModalService
        .openModal({
          title: 'Edit grid columns',
          content: RecordGridColumnEditorModalComponent,
          data: {
            columnParams: data
          },
          size: OpenViewActionModalSize.Large as any,
          testID: 'edit-grid-columns'
        })
        .then((result: IRecordGridColumnEditorModalResult) => {
          if (result && result.columns) {
            this.updateValue(result.columns);
          }
        })
        .catch(noop);
    });
  }

  focus(data: RecordGridDesignValidationIssue['data']): void {
    this.openEditor(data?.columnGuid, data?.actionIndex);
  }

  getColumnResourceTypeName(column: IRecordGridDesignColumnData): string {
    const resourceType = RX_RECORD_DEFINITION.resourceTypesByFullName[column.fieldDefinition.resourceType];

    return get(RX_RECORD_DEFINITION.dataTypes[resourceType], 'displayName', '');
  }

  getSortedColumns(columns: IRecordGridDesignColumnData[]): IRecordGridDesignColumnData[] {
    return columns.sort((column1, column2) => column1.index - column2.index);
  }

  trackByForColumns(index: number, column: IRecordGridDesignColumnData): string {
    return column.fieldId;
  }

  isActionsColumn(fieldId: string): boolean {
    return fieldId === RX_RECORD_GRID.actionsColumnFieldDefinition.id;
  }

  ngOnDestroy(): void {
    this.updateColumnsSubject.complete();
  }
}
